.marker {
    r: 1;
    fill:rgb(226, 135, 43);
    /* transition: 0.5s; */
    cursor:pointer;
}

.maindiv {
    background-image: url("../Assets/leaf1.png");
}

.marker:hover {
    /* transition: 0.5s; */
    r: 2;
    fill: red;
    cursor:pointer;
    border-width: 10px;
    border-color: black;
    border-style: solid;
    border-radius: 1;
}

.highlightmarker {
    fill: red;
    r: 1.5;
    /* transition: 0.5s; */
    cursor:pointer;
}

.highlight:hover {
    /* transition: 0.5s; */
    r: 1.5;
    fill: red;
    cursor:pointer;
    border-width: 10px;
    border-color: black;
    border-style: solid;
    border-radius: 1;
}

